.sh__omenuUse {
    display: flex;
    justify-content: center;
}

.sh__omenuUse-container {
    display: flex;
    justify-content: center;
    flex-direction: row;
    padding-top: 4rem;
}

.sh__omenuUse-container-content {
    display: flex;
    flex-direction: column;
}

.sh__omenuUse-container-content-title h1{
    display: flex;
    justify-content: flex-end;
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 64px;
    color: #FFF;
}

.sh__omenuUse-container-content-description {
    padding-top: 2rem;
}

.sh__omenuUse-container-content-description p{
    display: flex;
    justify-content: flex-start;
    font-family: var(--font-family);
    font-size: 18px;
    color: #FFF;
}

.sh__omenuUse-container-content-description li{
    display: flex;
    justify-content: flex-start;
    font-family: var(--font-family);
    font-size: 18px;
    color: #FFF;
}

.sh__omenuUse-container-video {
    padding: 8rem;
}

