.sh__omenuDescription-container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.sh__omenuDescription-container-description {
    padding: 4rem;
}

.sh__omenuDescription-container-description h1{
    display: flex;
    justify-content: center;
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 64px;
    color: #000000;
}

.sh__omenuDescription-container-description p{
    display: flex;
    justify-content: center;
    font-family: var(--font-family);
    font-size: 14px;
    color: #000000;
    padding-left: 24rem;
    padding-right: 24rem;
}

.sh__omenuDescription-container-video {
    padding-bottom: 4rem;
}


